<template>
  <el-container v-loading="loading" element-loading-text="" element-loading-background="rgba(0, 0, 0, 0.75)" style="width: 100%; height: 100%;">
    <el-header style="background-color: #545c64; display: flex; align-items: center; justify-content: space-between;">

      <el-autocomplete v-model="keyword" :fetch-suggestions="querySearch" placeholder="搜索" :trigger-on-focus="false"
        @select="handleSelect">
        <i v-if="keyword" class="el-icon-close el-input__icon" slot="suffix" @click="handleIconClear"
          style="cursor: pointer;" />
        <i v-else class="el-icon-search el-input__icon" slot="suffix" />
        <template slot-scope="{ item }">
          <div :title="item.value" style="text-overflow: ellipsis; overflow: hidden; font-size: 12px; color: #666;">{{ item.value }}</div>
        </template>
      </el-autocomplete>
      <div style="flex-grow: 1;"></div>
      <div style="display: flex; column-gap: 30px;">
        <el-link v-for="(item, index) in geo_list" :key="index" @click="handleGeoClick(item, index)" icon="el-icon-folder" :type="current===index ? 'warning' : 'info'">{{item.name}}</el-link>
        <el-link @click="handleHome" icon="el-icon-setting" type="info">复位</el-link>
        <el-link @click="handleShow" icon="el-icon-setting" :type="visible ? 'warning' : 'info'">属性</el-link>
      </div>
    </el-header>
    <el-main style="padding: 0; position: relative; overflow: hidden;">
      <div id="cesiumContainer" style="width: 100%; height: 100%;"></div>
      <div
        style="position: absolute; top: 0; right: 0; width: 25%; height: 100%; background-color: rgba(255, 255, 255, 1); border-left: 1px solid #ccc; transition: all .2s linear;"
        :style="{ transform: visible ? 'translateX(0)' : 'translateX(100%)' }">
        <div style="width: 100%; height: 100%; overflow-y: auto">
          <table>
            <tr v-for="(item, index) in selectItem.properties">
              <td>{{ item.key }}</td>
              <td>{{ item.value }}</td>
            </tr>
          </table>
        </div>
      </div>
    </el-main>
  </el-container>
</template>
<script>
var viewer = null;
var DEFAULT_FILL_COLOR = new Cesium.Color(1, 1, 0, 0.39);
var DEFAULT_STROKE_COLOR = new Cesium.Color(1, 1, 0, 1);
var SELECT_FILL_COLOR = new Cesium.Color(1, 0, 0, 0.39);
var SELECT_STROKE_COLOR = new Cesium.Color(1, 0, 0, 1);
export default {
  name: "CesiumGeoJson",
  data() {
    return {
      current: 0,
      geo_list: geo_list,
      loading: false,
      visible: false,
      keyword: null,
      restaurants: [],
      selectItem: {},
    }
  },
  mounted() {
    viewer = new Cesium.Viewer("cesiumContainer", {
      imageryProvider: new Cesium.SingleTileImageryProvider({
        url: "data:image/gif;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQImWNgYGBgAAAABQABh6FO1AAAAABJRU5ErkJggg==",
      }),
      baseLayerPicker: false,
      animation: false,
      fullscreenButton: false,
      vrButton: false,
      geocoder: false,
      homeButton: false,
      infoBox: false,
      sceneModePicker: false,
      selectionIndicator: false,
      timeline: false,
      navigationHelpButton: false,
      navigationInstructionsInitiallyVisible: false,
      scene3DOnly: true,
      shouldAnimate: false,
      skyBox: false,
    });
    window.viewer = viewer;
    viewer.cesiumWidget.creditContainer.style.display = "none";

    // viewer.scene.globe.translucency.enabled = true;
    // viewer.scene.globe.undergroundColor = undefined;
    // viewer.scene.globe.showGroundAtmosphere = false;
    // viewer.scene.skyAtmosphere.show = false;
    // viewer.scene.globe.baseColor = Cesium.Color.TRANSPARENT;

    viewer.imageryLayers.addImageryProvider(new Cesium.WebMapTileServiceImageryProvider({
        url: "http://t0.tianditu.gov.cn/img_w/wmts?tk=" + td_key,
        layer: "img",
        style: "default",
        tileMatrixSetID: "w",
        format: "tiles",
        maximumLevel: 18
    }))

    viewer.imageryLayers.addImageryProvider(new Cesium.WebMapTileServiceImageryProvider({
        url: "http://t0.tianditu.gov.cn/cia_w/wmts?tk=" + td_key,
        layer: "cia",
        style: "default",
        tileMatrixSetID: "w",
        format: "tiles",
        maximumLevel: 18
    }))

    var handler = new Cesium.ScreenSpaceEventHandler(viewer.canvas);
    handler.setInputAction((movement) => {
      var pickedFeature = viewer.scene.pick(movement.position);
      if (Cesium.defined(pickedFeature) && pickedFeature.primitive) {
        const entity = pickedFeature.id;
        const properties = entity.properties;
        const propertyNames = properties.propertyNames;
        this.selectItem = {
          properties: propertyNames.map(key => {
            return { key, value: properties[key]._value }
          })
        }
        this.visible = true;
        this.clearEntitysColor(this.restaurants);
        this.selectEntityColor(entity);
      } else {
        if (!this.keyword) {
          this.visible = false;
          this.selectItem = {};
          this.clearEntitysColor(this.restaurants);
        }
      }
    }, Cesium.ScreenSpaceEventType.LEFT_CLICK);

    const { geo_list, current } = this;
    const currItem = geo_list[current];
    this.loadGeoJson(currItem.path);
    
  },
  methods: {
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return restaurant.value.toLowerCase().includes(queryString);
      };
    },
    handleSelect(item) {
      const { entity, properties } = item;
      this.selectItem = item;
      this.clearEntitysColor(this.restaurants);
      this.selectEntityColor(entity);
      viewer.flyTo(entity);
    },
    handleIconClear() {
      this.selectItem = {};
      this.keyword = null;
      this.clearEntitysColor(this.restaurants);
    },
    handleGeoClick(item, index) {
      viewer.dataSources.removeAll();
      this.current = index;
      this.loadGeoJson(item.path);
    },
    handleHome() {
      viewer.flyTo(viewer.dataSources._dataSources[0]);
    },
    handleShow() {
      this.visible = !this.visible;
    },
    clearEntitysColor(entities) {
      entities.forEach(({ entity }) => {
        entity.polygon.extrudedHeight = 0;
        entity.polygon.outlineColor = DEFAULT_STROKE_COLOR;
        entity.polygon.material = DEFAULT_FILL_COLOR;
      })
    },
    selectEntityColor(entity) {
      entity.polygon.extrudedHeight = 0.1;
      entity.polygon.outlineColor = SELECT_STROKE_COLOR;
      entity.polygon.material = SELECT_FILL_COLOR;
    },
    loadGeoJson(geojsonUrl) {
      this.loading = true;
      Cesium.GeoJsonDataSource.load(geojsonUrl)
      .then((dataSource) => {
        const entities = dataSource.entities.values;
        entities.forEach(entity => {
          entity.polygon.outline = true;
          entity.polygon.outlineWidth = 3;
          entity.polygon.outlineColor = DEFAULT_STROKE_COLOR;
          entity.polygon.material = DEFAULT_FILL_COLOR;
        })
        const restaurants = entities.map(entity => {
          const properties = entity.properties;
          const propertyNames = properties.propertyNames;
          return {
            value: propertyNames.map(key => {
              return properties[key];
            }).toString(),
            entity,
            properties: propertyNames.map(key => {
              return { key, value: properties[key]._value }
            })
          }
        })
        this.restaurants = restaurants;
        viewer.dataSources.add(dataSource);
        viewer.zoomTo(dataSource).then(() => {
          this.loading = false;
        });
      })
    }
  }
}
</script>
<style>
table {
  width: 100%;
  /* 设置表格宽度 */
  border-collapse: collapse;
  /* 合并单元格边框 */
  background-color: #f9f9f9;
  /* 背景颜色 */
  border: 1px solid #ddd;
}

table th,
table td {
  padding: 10px;
  /* 单元格内部的填充 */
  text-align: left;
  /* 文本对齐方式 */
  border: 1px solid #ddd;
  /* 给所有单元格添加边框 */
}

table thead {
  background-color: #ccc;
  /* 设置表头背景色 */
}

table th {
  font-weight: bold;
  /* 表头文字加粗 */
}

table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
  /* 设置奇偶行交替背景色 */
}

table td:first-child {
  border-left: none;
  /* 去掉首列左边框 */
}

table td:last-child {
  border-right: none;
  /* 去掉末列右边框 */
}
</style>